.Profile {
    text-align: center;
}

.container-wrapper {
    min-height: 100vh;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    background-image: linear-gradient(to right top, #85c1e9, #a8b4ec, #d1a4da, #ed97b6, #f1948a);
}

.container {
    min-height: auto;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    padding-top: 50px;
    max-width: 960px;

}

.img {

    align-items: center;
    margin: 15px;
}

.text {
    color: rgba(0, 0, 0, 0.7);
    align-items: center;
    margin: 15px;
    padding-left: 80px;
}

.text h1 {
    text-align: left;
    font-size: 40pt;
    margin-bottom: 0px;
    margin-top: 0px;
    letter-spacing: 1pt;
}

.text h2 {
    letter-spacing: 2.5pt;
    text-align: left;
    font-weight: 400;
    font-style: italic;
    margin: 0px;
}

.text p {
    text-align: left;
    font-size: 16pt;
    letter-spacing: 1.5pt;
    font-weight: 100;
}